import { useState, useEffect } from 'react';

const useTimeZone = () => {
  const [timeZone, setTimeZone] = useState('');

  useEffect(() => {
    const tz = Intl.DateTimeFormat().resolvedOptions().timeZone;
    setTimeZone(tz);
  }, []);

  return timeZone;
};

export default useTimeZone;
